import Vue from 'vue'
import Vuex from 'vuex'

// moudles
import auth from "./auth";
import snackbar from "./snackbar";
import api from './api'
Vue.use(Vuex)

export default new Vuex.Store({
  state:{
    error: '',
    success: ''
  },
  mutations: {
    error (state, message) {
      state.error = message
    },
    success (state, message) {
      state.success = message
    }
  },
  modules: {
    snackbar,
    auth,
    api
  }
})
